import React, { Component } from 'react';

import Campsite from "./Campsite"

const weekDay = [ "Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat" ];
const monthName = [ 'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December' ];

function formatDay(date){
    let d = new Date(date);
    let dayOfWeek = weekDay[d.getDay()] + " " + monthName[d.getMonth()] + ", " + d.getFullYear();
    return dayOfWeek;
}

class Booker extends Component {
    constructor(props){
        super(props);
    }

    render(){
        const { rsp, params} = this.props;
        return(
            <div>
                 <div className="campsites Booker">
                        <div className="parameter-tag-wrapper">
                            { params.searchterm 
                            ? 
                                <div className="parameter-tag">Searchterm:  {params.searchterm}</div>
                            : null
                            }
                            { params.startdate 
                            ? <div className="parameter-tag">Start Date:    {params.startdate}</div>
                            : null
                            }
                            { params.enddate 
                            ? <div className="parameter-tag">End Date: {params.enddate}</div>
                            : null
                            }
                            { params.numoccupants 
                            ? <div className="parameter-tag">People: {params.numoccupants}</div>
                            : null
                            }
                            { params.desirednightsstayed 
                            ? <div className="parameter-tag">Nights:    {params.desirednightsstayed}</div>
                            : null
                            }
                        </div>
                        <div>
                            {Object.keys(rsp).map( (key, index) =>
                                <div className="booking option">
                                    <h2>{key}</h2>
                                    <table>
                                        <th></th><th></th><th></th>
                                        {rsp[key].map( (campsite, yindex) =>
                                            <tr>
                                                {console.log(campsite.url)}
                                                <td>{formatDay(campsite.startDate) + " to " + formatDay(campsite.endDate)}</td>
                                                <td>Campsite # {campsite.campsiteNumber}</td>
                                                <td><a href={campsite.url}>Book</a></td>
                                            </tr>
                                        )}
                                    </table>
                                </div>
                            )}
                        </div>     
                    </div>
            </div>
        );
    }
}

export default Booker;



