import React, { Component } from 'react';

import Campsite from "./Campsite"
import axios from 'axios';

const endpoint = "https://txolc09jb5.execute-api.us-east-2.amazonaws.com/dev/booksites";

class Gateway extends Component {
    constructor(props){
        super(props);
        this.state = { password:null }

        this.onClick = this.onClick.bind(this);
        this.onChangeGateway = this.onChangeGateway.bind(this);
        this.onKeyPressGateway = this.onKeyPressGateway.bind(this);
    }

    onKeyPressGateway(event){
        if(event.charCode === 13){
            event.preventDefault();
            this.props.onClickGateway(this.state.password);
        }
    }

    onChangeGateway(event){
        this.setState({ [event.target.name]:event.target.value });
    }

    onClick(e){
        console.log(this.state.password)
        this.props.onClickGateway(this.state.password);
    }

    render(){
        const { password, access } = this.props
        return(
            <div>
                <div className="Password">
                    <form className="passwordInput">
                        <input
                            className="input-area-password"
                            type="text"
                            name="password"
                            placeholder="password"
                            value={password}
                            onChange={this.onChangeGateway}
                            onKeyPress={this.onKeyPressGateway}
                        />
                        <div className="button-wrapper"> 
                            <button className="submit-button bttn"
                                onClick={this.onClick}
                                type="button"
                            >
                                Enter passphrase
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        );   
    }


}

export default Gateway;



