import React, { Component } from 'react';
//import Finder from "./components/Finder";
import Finder from "./components/Finder";
import axios from "axios";
import Error from "./components/Error";
import Gateway from './components/Gateway';
import Booker from './components/Booker';

import './App.css';

const endpointGateway = "http://ec2-18-217-242-69.us-east-2.compute.amazonaws.com/validatecode";
const endpointFinder = "http://ec2-18-217-242-69.us-east-2.compute.amazonaws.com/booksites"

class App extends Component {
  constructor(props){
    super();
    
    this.state = {
        password:"",
        key:null, 
        error:null,
        isCalOpen: false,
        response:null,
        params: null
    }

    this.onClickGateway = this.onClickGateway.bind(this);
    this.pageClick = this.pageClick.bind(this);
    this.onFinderClick = this.onFinderClick.bind(this);
  }

 render(){
    const { key, password, error, isCalOpen, searching, response, params } = this.state;
    return (
      <div className="App">
        <div className="page">
          <h1 className="title">Summer Land</h1>
              { key
                ? <Finder
                    searching={searching}
                    accesstoken={key}
                    isCalOpen={isCalOpen}
                    onFinderClick={this.onFinderClick}
                  />
                : <Gateway 
                    onClickGateway={this.onClickGateway}
                  />
              } 
        </div>
        <div className="booker">
              { response 
                ? <Booker 
                    params={params}
                    rsp={response}
                  />
                : null
              }
        </div>
      </div>
    );
  }
  componentDidMount(){
    window.addEventListener('mousedown', this.pageClick, false);
  }

  pageClick(){
    this.setState({isCalShown:false})
  }

  onClickGateway(password){
    //let params = JSON.stringify({"password": "summerland"});
    let params = JSON.stringify({"password":password});
    console.log("sendsend");
    axios({
        method: 'POST',
        url: endpointGateway,
        data: params,
        config : {headers: { 
                "Access-Control-Allow-Methods": "POST, GET, OPTIONS",
                "Access-Control-Allow-Origin": "http://ec2-18-191-219-27.us-east-2.compute.amazonaws.com:3000/validatecode",
                "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept,  application/json",
                "Content-Type": 'application/json'
            }}
      }).then((rsp) => {
        console.log(rsp.data);
        this.setState({key:rsp.data, searching:false})
      }).catch((error) => {
        console.log( error.response )
      });
      this.setState({searching:true})
    }

    onFinderClick(p)
    {
      let params = p;
      params['accesstoken'] = this.state.key;
      

      axios( { method: 'POST',
                url:endpointFinder,
                data: JSON.stringify(params),
                config : {headers: { 
                    "Access-Control-Allow-Origin": "*",
                    "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept,  application/json",
                    "Content-Type": 'application/json'
              }}
      }).then( (rsp) => {
          let data = rsp.data;
          console.log( JSON.stringify(rsp.data) );
          this.setState( {response: data, params:params, searching: false} );
      }).catch( error => {
          console.log( error.response )
          this.setState({searching:false})
      });

      this.setState({searching:true, response:null});
    }
}

export default App;



