import React, { Component } from 'react';

class Error extends Component {
    constructor(props){
        super();
    }

    render(){
        return(
            <div className="Error">
                <div>
                    Password was incorrect, please try again or contact us at summersummerland@gmail.com
                </div>
            </div>
        );
    }
}

export default Error;


