import React, { Component } from 'react';

import Booker from "./Booker";
import Loader from "./Loader";

import CalendarLR from "./CalendarLR";

import axios from "axios"; 

const endpoint = "http://ec2-18-217-242-69.us-east-2.compute.amazonaws.com/booksites";
const weekDay = [ "Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat" ];
const monthName = [ 'January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December' ];

function getDateStrings(value)
{
    let d = new Date(value);
    return dateStrings(d)
}

function dateStrings(d)
{
    let day = d.getDay();
    let date = d.getDate();
    let year = d.getFullYear();
    let month = d.getMonth();
    let dStr = ('0' + date).slice(-2) + "/" + ("0" + month).slice(-2) + "/" + year;
    let dateStr = weekDay[day] + ", " +  monthName[month] + " " + date;
    let dStrObj = ('0' + (month + 1) ).slice(-2) + "/" + ("0" + date).slice(-2) + "/" + year;
    return ({dStr:dStr, dateStr:dateStr, dStrObj: dStrObj});
}

class Finder extends Component 
{
    constructor(props)
    {
        super(props);
        const initialDate = dateStrings(new Date());

        this.state = 
        {
            accesstoken:this.props.accesstoken,
            searchterm:'',
            start:initialDate,
            end:initialDate,
            numoccupants:'',
            desirednightsstayed:'',
            response:null,
            left: null,
            right: null,
        }
        
        this.onButtonClick = this.onButtonClick.bind(this);
        this.onCalClick = this.onCalClick.bind(this);
        this.onChangeClickLeft = this.onChangeClickLeft.bind(this);
        this.onChangeClickRight = this.onChangeClickRight.bind(this);

        this.onChange = this.onChange.bind(this);
    }


    render(){
        const 
        { searchterm, start, end, numoccupants, desirednightsstayed, params, left, right, isCalOpen } = this.state;
        const { accesstoken, response, searching } = this.props;
        return(
            <div>
             <div className="Finder">
                { ! searching 
                ? <div id="finder-area">
                <h2>Find a campsite, perfect for you</h2>
                <form>
                    <div className="input-wrapper">
                        <span>Key Term</span>
                        <input
                            name="searchterm"
                            type="text"
                            onChange={this.onChange}
                            placeholder="Place or Keyword"
                            value={searchterm}
                        />
                    </div>
                    <div className="calendar-wrapper">
                        <div className="calendar input-wrapper">
                            <span>Start and End Date</span>
                            <input
                                name="startdate"
                                type="text"
                                onChange={this.onChange}
                                onClick={this.onCalClick}
                                value={start.dateStr}
                            />
                            { left 
                                ? <CalendarLR
                                    value={ new Date(start.dStrObj) }
                                    className="calendar-c start"
                                    onClickDay={this.onChangeClickLeft}
                                />
                                :null
                            }
                        </div>
                        <div className="calendar input-wrapper">
                            <input
                                name="enddate"
                                type="text"
                                onClick={this.onCalClick}
                                value={end.dateStr}
                            />
                            { right 
                                ? <CalendarLR
                                    activeStartDate={new Date(start.dStrObj)}
                                    value={new Date(end.dStrObj)}
                                    className="calendar-c end"
                                    onClickDay={this.onChangeClickRight}
                                />
                                : null
                            }
                        </div>
                    </div>
                    <div className="input-wrapper">
                        <span>People</span>
                        <input
                            name="numoccupants"
                            type="text"
                            onChange={this.onChange}
                            placeholder="# people"
                            value={numoccupants}
                        />
                    </div>
                    <div className="input-wrapper">
                        <span>Nights</span>
                        <input
                            name="desirednightsstayed"
                            type="text"
                            onChange={this.onChange}
                            placeholder="Desired # of Nights"
                            value={desirednightsstayed}
                        />
                    </div>
                    <button className="submit-button bttn"
                        onClick={this.onButtonClick}
                        type="button"
                    >
                        Find Sites
                    </button>
                </form>
                </div>
                : <Loader /> }
            </div>
            </div>
        );
    }

    onChangeClickLeft = (value) => 
    {
        let start = getDateStrings(value);
        this.setState({start:start, left:false});
    }

    onChangeClickRight = (value) => 
    {
        let end = getDateStrings(value);
        this.setState({end:end, right:false});
    }

    onCalClick(e)
    {
        if(e.target.name == "startdate") {
            this.setState({left:true, right:false});
        } else{
            this.setState({right:true, left:false});
        }
    }

    onButtonClick()
    {        
        let params = {
            accesstoken:this.state.accesstoken,
            searchterm:this.state.searchterm,
            startdate:this.state.start.dStrObj,
            enddate:this.state.end.dStrObj,
            numoccupants:this.state.numoccupants,
            desirednightsstayed:this.state.desirednightsstayed,
        };
        this.props.onFinderClick(params);
        this.setState({searching:true});
    }
    
    onChange(event)
    {
      this.setState({ [event.target.name] : event.target.value});
    }

}

export default Finder;





